.logo{
    color: $color_logo;
    // color: yellow;
    margin-left: 4rem;
    .logo_nombre{
        font-family: $fuente_logo;
        font-weight: 300;
        font-size: 3rem;
    }
    .logo_bold{
        font-family: $fuente_logo;
        font-weight: 700;
        font-size: 3rem;
    }
    @include telefono{
        margin-left: inherit;
    }
    @include ipad{
        margin-left: inherit;
    }
    @include ipadPro{
        margin-left: inherit;
    }
}
html.dark .logo{
    color: $color_negro;
}

.nav{
    a{
        color: $color_azul_oscuro;
        margin-right: 2rem;
        padding-bottom: 1rem;
        font-family: $fuente_nav;
        position: relative;
        &:last-of-type{
            margin-right: 0;
        }
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 0;
            height: 2px;
            background-color: $color_azul_oscuro;
            transition: width .3s linear;
        }
        &:hover::after{
            width: 100%;
        }
        @include telefono{
            margin-bottom: 1.2rem;
            margin-right: inherit;
        }
    }
    @include ipad{
        margin-bottom: 2rem;
    }
    @include ipadPro{
        margin-bottom: 2rem;
    }
    span{
        font-family: $fuente_nav;
        color: $color_azul_claro;
        letter-spacing: -.2rem;
        font-size: 2rem;
        transition: color .3s linear;
        &:hover{
            color: $color_azul_oscuro;
        }
    }
    @include telefono{
        display: flex;
        flex-flow: column;
    }
}
html.dark .nav{
    a{
        color: $color_azul_dark;
        &::after{
            background-color: $color_azul_dark;
        }
    }
    span{
        color: $color_blanco;
        &:hover{
            color: $color_azul_dark;
        }
    }
}

.boton{
    width: 5rem;
    height: 5rem;
    appearance: none;
    box-shadow: 
    -5px -5px 15px rgb(0, 0, 0),
    2px 2px 15px rgba(13, 12, 17, 0.63),
    inset -5px -5px 15px rgb(0, 0, 0),
    inset 2px 2px 15px rgba(43, 41, 51, 0.493);
    border-radius: 50%;
    border: 3px solid $color_fondo;
    outline: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    &::after{
        font-family: FontAwesome;
        content: "\f186";
        font-size: 2rem;
        color: $color_verde;
    }
    &:checked{
        border: 3px solid $color_fondo_dark;
        box-shadow: 
        -5px -5px 15px rgb(184, 204, 228),
        2px 2px 15px rgba(113, 137, 168, 0.63),
        inset -5px -5px 15px rgb(166, 179, 223),
        inset 2px 2px 15px rgba(129, 158, 212, 0.493);
    }
    &:checked::after{
        color: rgba(0, 4, 255, 0.589);
    }
}

.curriculum{
    border: #A3EEFA solid 1px;
    border-radius: 2rem;
    margin-right: 4rem;
    padding: 1.2rem;
    color: #CFCED3;
    @include telefono{
        margin-right: inherit;
        margin-top: 2rem;
    }
    @include ipad{
        margin-top: 2rem;
        margin-right: inherit;
    }
    @include ipadPro{
        margin-top: 2rem;
        margin-right: inherit;
    }
}

html.dark .curriculum{
    border: $color_negro solid 1px;
    color: #271663;
}

.info{
    width: 60%;
    margin: 12rem auto 30rem;
    cursor: default;
    h1{
        font-size: 7rem;
        margin: 0;
        font-weight: bold;
        color: #CFCED3;
        @include telefono{
            font-size: 6rem;
            line-height: 1;
        }
    }
    h2{
        font-size: 5.6rem;
        font-weight: normal;
        color: #83769e;
        margin-top: -1rem;
        margin-bottom: 2.5rem;
        @include telefono{
            margin-top: 1.4rem;
            font-size: 4.5rem;
            line-height: 1;
        }
        @include ipad{
            margin-top: .6rem;
            font-size: 5rem;
        }
    }
    p{
        font-family: $fuente_p;
        font-weight: 300;
        line-height: 1.5;
        color: rgba(255, 255, 255, 0.774);
        margin-bottom: 3rem;
    }
    @include telefono{
        width: 90%;
    }
    @include ipad{
        width: 90%;
    }
    @include ipadPro{
        width: 90%;
    }
}
html.dark .info{
    h1{
        color: #0f0f33;
    }
    h2{
        color: #83769e;
    }
    p{
        color: rgb(12, 12, 12);
        font-weight: 700;
    }
}