.contacto{
    padding-top: 5rem;
    .contenido{
        width: 100%;
        background-color: $color_azul_contacto;
        filter: brightness(80%);
        box-shadow: $color_sombra_card 0px 0px 1rem;
        border-radius: 3rem;
        // filter: blur(5px);
        form{
            font-family: $fuente_p;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-flow: column;
            margin: 0 auto 7rem auto;
            width: 80%;
            padding: 2rem;
            @include telefono{
                width: 100%;
            }
            input:-webkit-autofill,
            input:-webkit-autofill:hover, 
            input:-webkit-autofill:focus, 
            input:-webkit-autofill:active  {
                box-shadow: 0 0 0 30px inset rgb(4, 0, 26) !important;
                -webkit-text-fill-color: white;
                filter: brightness(80%);
            }
            input, textarea{
                border: none;
                resize: none;
                outline: none;
                
                color: $color_blanco;
                background-color: $color_azul_contacto;
                
                padding: 1.5rem 0;
                margin: 2rem;
                width: 80%;
                
                border-bottom: 1px solid rgb(160, 158, 158);
                transition: border-bottom .3s ease;
                &:focus{
                    border-bottom: 2px solid $color_blanco;
                }
                &::placeholder{
                    color: $color_blanco;
                }
            }
            input[type=submit]{
                font-family: $fuente_p;
                max-width: 20rem;
                border: none;
                padding: 2rem;
                border-radius: 3rem;
                margin-top: 3rem;
                transition: box-shadow .3s ease;
                background-color: $color_azul_contacto;
                &:hover{
                    box-shadow: $color_sombra_card 0px 0px 1rem;
                }
                @include telefono{
                    box-shadow: $color_sombra_card 0px 0px 1rem;
                }
            }
        }
    }
}
html.dark .contenido{
    background-color: $color_blanco;
    box-shadow: $color_negro 0px 0px 2rem;
    input, textarea{
        color: $color_negro;
        background-color: $color_blanco;
        &:focus{
            border-bottom: 2px solid $color_negro;
        }
        &::placeholder{
            color: $color_negro;
        }
    }
    input[type=submit]{
        background-color: $color_blanco;
    }
}