.nav-rrss{
    display: flex;
    padding-left: 4rem;
    @include telefono{
        padding-left: 0;
        justify-content: center;
    }
    a{
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 5rem;
        height: 5rem;
        text-decoration: none;
        margin: 0 1rem;
        transition: 0.5s;
        span{
             position: absolute;
            transition: transform .5s;
            &:nth-child(1),
            &:nth-child(3){
                width: 100%;
                height: 1px;
                background: #a0a0a0;
            }
            &:nth-child(1){
                top: 0;
                left: 0;
                transform-origin: right;
            }
            &:nth-child(3){
                bottom: 0;
                left: 0;
                transform-origin: left;
            }
            &:nth-child(2),
            &:nth-child(4){
                width: 2px;
                height: 100%;
                background: $color_blanco;
            }
            &:nth-child(2){
                top: 0;
                left: 0;
                transform: scaleY(0);
                transform-origin: bottom;
            }
            &:nth-child(4){
                top: 0;
                right: 0;
                transform: scaleY(0);
                transform-origin: top;
            }
            @include telefono{
                &:nth-child(1),
                &:nth-child(3){
                    width: 100%;
                    height: 1.5px;
                    background: #a0a0a0;
                }
            }
        }
        &:hover{
            span:nth-child(1){
                transform: scaleX(0);
                transform-origin: left;
                transition: transform .5s;
            }
            span:nth-child(3){
                transform: scaleX(0);
                transform-origin: right;
                transition: transform .5s;
            }
            span:nth-child(2){
                transform: scaleY(1);
                transform-origin: top;
                transition: transform .5s;
            }
            span:nth-child(4){
                transform: scaleY(1);
                transform-origin: bottom;
                transition: transform .5s;
            }    
        }
    }
}
html.dark .nav-rrss{
    a{
        span{
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4){
                background-color: $color_negro;
            }
        }
    }
}

