.habilidades{
    padding-top: 5rem;
    cursor: default;
    margin-bottom: 15rem;
    .grid{
        display: grid;
        grid-template-columns: 70% 22%;
        column-gap: 8%;
        .habilidades-info{
            p{
                font-family: $fuente_p;
                color: $color_azul_oscuro;
                line-height: 2rem;
            }
            .lista-habilidades{
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                line-height: 5rem;
                li{
                    font-family: $fuente_p;
                    color: #b9a9a9;
                    transition: color .3s ease-in-out;
                    &:hover{
                        color: $color_blanco;
                        text-shadow: #b9a9a9 0 0 1rem;
                        font-weight: bold;
                    }
                    @include telefono{
                        color: $color_blanco;
                        text-shadow: #b9a9a9 0 0 1rem;
                    }
                    // &:last-of-type{
                    //     display: none;
                    //     @include telefono{
                    //         display: list-item;
                    //     }
                    // }
                }
                @include telefono{
                    grid-template-columns: 50% 50%;
                }
                @include ipad{
                    grid-template-columns: 33.3% 33.3% 33.3%;
                }
            }
        }
        img{
            filter: brightness(60%) blur(2px);
            box-shadow: rgb(7, 3, 66) 0 0 5rem;
            border-radius: 2rem;
            transition: filter .5s ease;
            &:hover{
                filter: brightness(100%);
            }
            @include telefono{
                filter: none;
                display: block;
                margin: 4rem auto 0;
                height: 30rem;
            }
            @include ipad{
                filter: none;
            }
        }
        @include telefono{
            display: block;
        }
        @include ipad{
            grid-template-columns: 65% 25%;
            column-gap: 10%;
        }
    }
}
html.dark .grid{
    .habilidades-info{
        p{
            color: #260572;
            font-weight: bold;
        }
        .lista-habilidades{
            li{
                color: #636060;
                &:hover{
                    color: $color_negro;
                    text-shadow: none;
                    font-weight: bold;
                }
            }
        }
    }
    img{
        box-shadow: #240077 0 0 5rem;
    }
}