html{
    box-sizing: border-box;
    font-size: 62.5%;
    scrollbar-width: none;
}
::-webkit-scrollbar{
    display: none;
}

*, *::before, *::after{
    box-sizing: inherit;
    font-size: 1.6rem;
    font-family: $fuente_default;    
}

body{
    background-color: $color_fondo;
    transition: background-color .5s linear;
}
html.dark body{
    background-color: $color_fondo_dark;
}

a{
    text-decoration: none;
}

img{
    max-width: 100%;
}

h1, h2, h3, h4{
    font-family: $fuente_heading;
}
h4{
    display: inline-block;
    cursor: default;
    color: $color_azul_claro;
    font-size: 2.4rem;
    transition: color .3s ease;
    &:hover{
        color: $color_azul_oscuro;
    }
    span{
        font-weight: bold;
        color: $color_azul_oscuro;
    }
}
html.dark h4{
    color: rgb(41, 44, 82);
    &:hover{
        color: $color_azul_dark;
    }
    span{
        color: rgb(13, 53, 68);
    }
}

//Clases Reutilizables
.display-flex{
    margin-top: -1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include telefono{
        flex-flow: column;
    }
    @include ipad{
        flex-flow: column;
    }
    @include ipadPro{
        flex-flow: column;
    }
}

.contenedor{
    max-width: 110rem;
    width: 90%;
    margin: 0 auto;
}

