.portafolio{
    padding-top: 5rem;
    margin-bottom: 15rem;
    .cards{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 5rem;
        cursor: default;
        @include telefono{
            grid-template-columns: none;
            grid-template-rows: repeat(1, 1fr);
            // grid-template-rows: 25%;
        }
        @include ipad{
            grid-template-columns: repeat(2, 1fr);
        }
    }
    .card{
        background-color: $color_azul_card;
        border-radius: 3rem;
        margin-bottom: 4rem;
        transition: box-shadow .4s ease;
        .container{
            position: relative;
            clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
            img{
                width: 100%;
                display: block;
                border-radius: 3rem 3rem 0 0;
            }
        }
        .detalles{
            padding: 2rem 1rem 0 1rem;
            h4{
                color: rgb(209, 208, 208);
                font-weight: 600;
                font-size: 3rem;
                margin: 1rem 0 1.5rem 0;
            }
            p{
                color: #a0a0a0;
                font-size: 1.5rem;
                line-height: 2rem;
                font-family: $fuente_p;
                font-weight: 300;
            }
        }
        .enlace-card{
            display: block;
            text-align: center;
            font-size: 2rem;
            font-family: $fuente_p;
            font-weight: 600;
            color: rgb(209, 208, 208);
            border-radius: 3rem;
            padding-bottom: 2rem;
        }
        @include telefono{
            // box-shadow: $color_sombra_card 0 0 3rem;
            // box-shadow: rgb(24, 0, 94) 0 0 3rem;
            box-shadow: rgb(14, 2, 48) 0 0 3rem;
            margin-top: 5rem;
        }
        &:hover{
            box-shadow: $color_sombra_card 0 0 3rem;
            // box-shadow: rgb(14, 2, 48) 0 0 3rem;
        }
    }
    
    .container::after{
        content: "";
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 3rem 3rem 0 0;
        opacity: 0.7;
    }
    .card-1>.container:after{
        background-image: linear-gradient(135deg, #0100ec, #fb37f4);
    }
    .card-2>.container:after{
        background-image: linear-gradient(135deg, #a5f4f7, #185fe2);
    }
    .card-3>.container:after{
        background-image: linear-gradient(135deg, #ceec61, #723320);
    }
    .card-4>.container:after{
        background-image: linear-gradient(135deg, #e23939, #a05959);
    }
}

html.dark .card{
    &:hover{
        box-shadow: $color_negro 0 0 3rem;
    }
}