footer{
    text-align: center;
    margin-bottom: 2rem;
    p{
        cursor: default;
        margin-bottom: 1rem;
        font-family: $fuente_nav;
        color: $color_blanco;
        span{
            color: $color_azul_oscuro;
            font-family: $fuente_nav;
            transition: color .4s ease;
            &:hover{
                color: $color_fondo;
            }
        }
    }
}
html.dark footer{
    p{
        color: $color_negro;
        span{
            color: rgb(28, 4, 116);
            &:hover{
                color: #bbdaf3;
            }
        }
    }
}